<template>
  <div class="u-cell" :class="classes">
    <div class="u-cell-wrap">
      <CellRender v-if="columnType === 'render'"
        :row="col.row" :render="col.column.render" :column="col.column"
        :value="col.value"></CellRender>
      <div v-if="columnType === 'normal'" v-html="value" class="u-cell-text" :class="{nowrap:nowrap}"></div>
      <CellEditor v-if="columnType === 'editor'"
        :col="col"
        :display="value"
        :row="col.row"
        :nowrap="nowrap"
        :editor-options="col.column.editorOptions"
        :edit-row="savingRow"
        :column="col.column"></CellEditor>
      <template v-if="columnType === 'check' && checkable">
        <i v-if="col.row._selected" class="ivu-icon ivu-icon-android-checkbox-outline u-cell-checkbox" @click.stop="handleCheckClick"></i>
        <i v-else class="ivu-icon ivu-icon-android-checkbox-outline-blank u-cell-checkbox" @click.stop="handleCheckClick"></i>
      </template>
      <span v-if="columnType === 'index'" :class="{nowrap:nowrap}">
        {{ index }}
      </span>
    </div>
    <span v-if="comment" class="u-cell-comment"
      :class="comment.type" :title="comment.content"
      ></span>
  </div>
</template>

<script>
import {mapState, mapMethod} from '@/utils/utils.js'
import CellRender from './UCellRender'
import CellEditor from './UCellEditor'

export default {
  name: 'Cell',
  props: {
    col: Object,
    store: Object,
    row_index: Number
  },

  components: {
    CellRender,
    CellEditor
  },

  computed: {
    ...mapState('nowrap', 'start', 'editRow', 'editMode', 'onCheckable'),
    value () {
      let value = this.col.value
      if (this.col.column.format) {
        value = this.col.column.format(value, this.col.column, this.col.row)
      }
      return value
    },

    comment () {
      return this.getComment(this.col.row, this.col.column)
    },

    classes () {
      return this.getClass(this.col.row, this.col.column)
    },

    index () {
      return this.start + this.row_index
    },

    /*
     * 根据编辑模式不同，选择数据保存的row
     */
    savingRow () {
      if (this.editMode === 'row') return  this.col.row._editRow
      else return this.col.row
    },

    columnType () {
      let type = this.col.column.type
      if (type === 'index') return 'index'
      if (type === 'check') return 'check'
      if (type === 'column') {
        if (this.col.column.render) return 'render'
        if (this.col.column.editor) return 'editor'
        return 'normal'
      }
    },

    checkable () {
      let c = this.col.row._checkable
      if (this.onCheckable) {
        c = this.onCheckable(this.col.row)
        this.$set(this.col.row, '_checkable', c)
      }
      return c
    }
  },

  methods: {
    ...mapMethod('getComment', 'getClass'),
    handleCheckClick () {
      this.store.toggle(this.col.row)
    }
  }
}
</script>

<style lang="less">
.u-cell {
  width: 100%;
  height: 100%;
  position: relative;
  display: table;

  .u-cell-wrap {
    width: 100%;
    max-width: 0px;
    padding: 0;
    vertical-align: middle;
    display: table-cell
  }

  .u-cell-text {
    padding: 0 5px;
    word-wrap: break-word;
  }

  .u-cell-checkbox {
    cursor: pointer;
  }

  .u-cell-comment {
    border: 4px solid;
    border-color: transparent transparent transparent transparent;
    width: 0px;
    height: 0px;
    position: absolute;
    top: 0px;
    right: 0px;
    z-index: 5000;

    &.error {
      border-color: #b94a48 #b94a48 transparent transparent;
    }

    &.warning{
        border-color: #f89406 #f89406 transparent transparent;
    }

    &.success{
        border-color: #468847 #468847 transparent transparent;
    }

    &.info{
        border-color: #3a87ad #3a87ad transparent transparent;
    }

    &.changed{
        border-color: #f00 #f00 transparent transparent;
    }
  }
}
</style>



